.TopBar {
  font-size: 30px;
  color: #ff00ff;
  text-align: center;
  display: flex;
  border-bottom: 2px solid $spliter;
  & :nth-child(2) {
    min-width: 80%;
    padding-top: $padding;
    padding-bottom: $padding;
  }
  & :nth-child(1) {
    min-width: 10%;
    padding-top: $padding;
    padding-bottom: $padding;
    text-align: left;
  }
  & :nth-child(3) {
    min-width: 10%;
    padding-top: $padding;
    padding-bottom: $padding;
    text-align: right;
  }
}
